.App {
  text-align: center;
  background: linear-gradient(0deg, rgba(247,169,0,1) 16%, rgba(245,255,0,1) 100%, rgba(245,255,0,1) 100%);
}

.form{
  font-size: calc(20px + 5vmin); 
 text-align: center;
}
input[type=email] {
  width: 93%;
  padding: 15px 0px;
  margin: 8px 0px;
  box-sizing: border-box;
  border: 1px solid black;
  border-radius: 5px;
  text-align: center;
  color: black;
  background-color: transparent;
  position:relative; left:-0px; top:-80px;
}
input[type=password] {
  width: 93%;
  padding: 15px 0px;
  margin: 8px 0px;
  box-sizing: border-box;
  border: 1px solid black;
  border-radius: 5px;
  text-align: center;
  color: black;
  background-color: transparent;
  position:relative; left:-0px; top:-80px;
  

}
.button {
  background-color: white;
  color: black;
  box-sizing: border-box;
  border-radius: 6px;
  width: 75%;
  padding: 15px 0px;
  margin: 5px 0px;
  border: 1px solid #000;
  opacity: 0.85;
  position:relative; left:-0px; top:50px;
}

.logo{
  float: center; 
  position:relative; left:50px; top:-100px;
  margin: 8px 0px;
  
}
.register {
  position:relative; left:105px; top:50px;
}
.cuenta{
  position:relative; left:-40px; top:-108px;
}

.App-header {
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


